.ReactModalPortal{position:relative}.ReactModalPortal .modal-overlay{align-items:center;background:#0000006b;bottom:0;display:flex;justify-content:center;left:0;opacity:0;position:fixed;right:0;top:0;transition:opacity .2s ease-in-out;z-index:98}.ReactModalPortal .modal-overlay.ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .modal-overlay.ReactModal__Overlay--before-close{opacity:0}.ReactModalPortal .modal-content{display:flex;flex-direction:column;margin:20px;max-height:calc(100vh - 40px);max-width:calc(100vw - 40px);outline:none;transform:scale(.95);transition:transform .2s ease-in-out}@media (min-width:365px) and (max-width:950px){.ReactModalPortal .modal-content{width:calc(100vw - 40px)}}.ReactModalPortal .modal-content.ReactModal__Content--after-open{transform:scale(1)}.ReactModalPortal .modal-content.ReactModal__Content--before-close{transform:scale(.95)}.ReactModalPortal .modal-content-disabled{pointer-events:none}